import { ContainerModule, interfaces } from "inversify";
import {
    AccountConfig,
    AccountConfigProvider,
    AccountConfigData,
    PublicConfigProvider,
    PublicConfig,
    RuntimeDomainOption,
    EnvironmentConfig,
    AccountSidOption,
    accountConfigRTTI,
    environmentConfigRTTI,
    accountConfigProviderRTTI,
    publicConfigProviderRTTI,
    configDataContainerRTTI
} from "~/modules/config";
import { AccountConfigMock } from "~/modules/config/mock";
import { accountConfigUpdaterRTTI } from "./config.rtti";
import { environmentConfigMock } from "./EnvironmentConfig/EnvironmentConfig.mock";
import { AccountConfigUpdater } from "./AccountConfig/AccountConfigUpdater";
import { ConfigDataContainer } from "./AccountConfig/ConfigDataContainer";
import { getPublicConfigMock } from "./PublicConfigProvider/PublicConfigProvideMock/getPublicConfigMock";
import { XOR } from "~/utils/utilityTypes";
import { AccountConfigDataContainer } from "./AccountConfig/AccountConfigImpl/AccountConfigDataContainer/AccountConfigDataContainer";

export const configModuleContainerMock = new ContainerModule((bind: interfaces.Bind) => {
    bind<AccountConfig>(accountConfigRTTI).to(AccountConfigMock).inSingletonScope();
    bind<ConfigDataContainer<AccountConfigData>>(configDataContainerRTTI)
        .to(AccountConfigDataContainer)
        .inSingletonScope();
    bind<AccountConfigProvider>(accountConfigProviderRTTI).toProvider<AccountConfig>((context: interfaces.Context) => {
        return () => {
            const accountConfig = context.container.get<AccountConfig>(accountConfigRTTI);
            return Promise.resolve(accountConfig);
        };
    });
    bind<AccountConfigUpdater>(accountConfigUpdaterRTTI).toDynamicValue((context: interfaces.Context) => {
        return (_: string, config: Partial<AccountConfigData>) => {
            const currentConfig = context.container
                .get<ConfigDataContainer<AccountConfigData>>(configDataContainerRTTI)
                .get();
            const newConfig = { ...currentConfig, ...config };
            return Promise.resolve(newConfig);
        };
    });
    bind<EnvironmentConfig>(environmentConfigRTTI).toConstantValue(environmentConfigMock);
    bind<PublicConfigProvider>(publicConfigProviderRTTI).toProvider<PublicConfig>((context: interfaces.Context) => {
        return (option: XOR<AccountSidOption, RuntimeDomainOption>) => getPublicConfigMock(context.container, option);
    });
});
