import { ContainerModule, interfaces } from "inversify";
import {
    authenticatorRTTI,
    authenticatorFactoryRTTI,
    twilioIdpUrlRTTI,
    authenticatorDataContainerRTTI
} from "./auth.rtti";
import { AuthenticatorMock } from "./Authenticator/AuthenticatorMock/AuthenticatorMock";
import { createAuthenticatorMock } from "./AuthenticatorFactory/createAuthenticatorMock/createAuthenticatorMock";
import { AuthenticatorMockFactory } from "./AuthenticatorFactory/createAuthenticatorMock/AuthenticatorMockFactory";
import { TwilioIdpUrlFunction } from "./TwilioIdpUrlFunction/TwilioIdpUrl";
import { getTwilioIdpUrlMock } from "./TwilioIdpUrlFunction/TwilioIdpUrlMock/getTwilioIdpUrlMock";
import { AuthenticatorDataContainer } from "./AuthenticatorDataContainer/AuthenticatorDataContainer";
import { AuthenticatorDataContainerMock } from "./AuthenticatorDataContainer/AuthenticatorDataContainer.mock";

export const authModuleContainerMock = new ContainerModule((bind: interfaces.Bind) => {
    bind<AuthenticatorMock>(authenticatorRTTI).to(AuthenticatorMock).inSingletonScope();
    bind<AuthenticatorDataContainer>(authenticatorDataContainerRTTI)
        .to(AuthenticatorDataContainerMock)
        .inSingletonScope();
    bind<AuthenticatorMockFactory>(authenticatorFactoryRTTI).toFactory<AuthenticatorMock>(
        (context: interfaces.Context) => {
            return () => {
                return createAuthenticatorMock(context.container);
            };
        }
    );
    bind<TwilioIdpUrlFunction>(twilioIdpUrlRTTI).toDynamicValue((context: interfaces.Context) => {
        return (port?: string) => {
            return getTwilioIdpUrlMock(context.container, port);
        };
    });
});
