import { injectable } from "inversify";
import { Logger, LoggerLevelNumbers, Loglevel, LoggerLevel } from "~/modules/logger";

@injectable()
export class LoggerImplMock implements Logger {
    private readonly loglevel: Loglevel;

    constructor(loglevel: Loglevel) {
        this.loglevel = loglevel;
    }

    level: LoggerLevelNumbers = 1;

    trace = jest.fn();

    debug = jest.fn();

    log = jest.fn();

    info = jest.fn();

    warn = jest.fn();

    error = jest.fn();

    setLevel = (level: LoggerLevel) => {
        this.loglevel.setLevel(level);
    };

    getLevel = (): LoggerLevelNumbers => {
        return this.loglevel.getLevel();
    };
}
