import { ContainerModule, interfaces } from "inversify";
import { getLogger as getLoglevel } from "loglevel";
import { getLogger } from "./LoggerFactory/getLogger";
import { Logger, NewableLogger, LoggerSettings } from "./Logger/Logger";
import {
    newableLoggerRTTI,
    defaultPluginsRTTI,
    loggerFactoryRTTI,
    loglevelFactoryRTTI,
    loggerSettingsRTTI,
    loggerInstancesRTTI
} from "./logger.rtti";
import { LoggerPlugin, LoglevelFactory, LoggerInstances } from "./LoggerFactory/LoggerFactory";
import { LoggerImplMock } from "./Logger/LoggerMock/Logger.mock";
import { getDefaultLoggerSettings } from "./DefaultLoggerLevel/defaultLoggerLevel";

export const loggerModuleContainerMock = new ContainerModule((bind: interfaces.Bind) => {
    bind<LoglevelFactory>(loglevelFactoryRTTI).toConstantValue(getLoglevel);
    bind<interfaces.Factory<Logger>>(loggerFactoryRTTI).toFactory<Logger>((context: interfaces.Context) =>
        getLogger(context.container)
    );
    bind<NewableLogger>(newableLoggerRTTI).toConstructor(LoggerImplMock);
    bind<LoggerPlugin[]>(defaultPluginsRTTI).toConstantValue([]);
    bind<LoggerSettings>(loggerSettingsRTTI).toConstantValue(getDefaultLoggerSettings());
    bind<LoggerInstances>(loggerInstancesRTTI).toConstantValue(new Map());
});
