import { SyncMap } from "~/modules/sync";
import { SyncMapItemMock } from "~/modules/sync/SyncMapItem/SyncMapItem.mock";
import { FlexSdkError, ErrorCode } from "~/errors";

export class SyncMapMock implements SyncMap {
    private readonly items: Map<string, SyncMapItemMock> = new Map();

    uniqueName: string;

    sid: string;

    dateUpdated: Date;

    get = jest.fn((key: string) => {
        const item = this.items.get(key);
        if (!item) {
            return Promise.reject(new FlexSdkError(ErrorCode.SyncMapItemNotFoundError));
        }

        return Promise.resolve(item);
    });

    on = jest.fn();

    removeListener = jest.fn();

    $add<T extends object = object>(item: SyncMapItemMock<T>): void {
        const existingItem = this.items.get(item.key);

        if (existingItem) {
            existingItem.$update(item.value);
        } else {
            this.items.set(item.key, item);
        }
    }

    $createEmptyItem(key: string): SyncMapItemMock {
        const item = new SyncMapItemMock(key);
        this.items.set(key, item);
        return item;
    }
}
