import { injectable, inject } from "inversify";
import { Session } from "~/modules/session";
import { Authenticator, authenticatorRTTI, Role } from "~/modules/auth";
import { Twilsock, twilsockRTTI } from "~/modules/websocket";

@injectable()
export class SessionMock implements Session {
    public token: string = "token";

    private readonly auth: Authenticator;

    private readonly connection: Twilsock;

    public roles: Array<Role> = [];

    constructor(@inject(authenticatorRTTI) auth: Authenticator, @inject(twilsockRTTI) connection: Twilsock) {
        this.auth = auth;
        this.connection = connection;
    }

    init = jest.fn((token: string) => {
        this.token = token;
        return Promise.resolve();
    });

    updateToken = jest.fn(() => {
        return Promise.resolve();
    });

    destroy = jest.fn(() => Promise.resolve());
}
