import { injectable } from "inversify";
import { EventPublisher } from "~/modules/events";
import { Sync, SyncEvent } from "~/modules/sync";
import { SyncMapMock } from "~/modules/sync/SyncMap/SyncMap.mock";
import { MapMode } from "~/modules/sync/Sync/MapMode";
import { FlexSdkError, ErrorCode } from "~/errors";

@injectable()
export class SyncMock extends EventPublisher<SyncEvent> implements Sync {
    private readonly maps = new Map<string, SyncMapMock>();

    isConnected = jest.fn(() => false);

    connect = jest.fn(() => {
        return Promise.resolve();
    });

    
    getMapById = jest.fn((mapId: string, mapMode: MapMode = MapMode.OpenExisting) => {
        let map = this.maps.get(mapId);
        if (!map) {
            if (mapMode === MapMode.OpenExisting) {
                return Promise.reject(
                    new FlexSdkError(
                        ErrorCode.SyncNameNotFoundError,
                        "Unique name not found (status: 404, code: 54300)"
                    )
                );
            }

            map = new SyncMapMock();
            map.sid = mapId;
            map.dateUpdated = new Date();
            this.maps.set(mapId, map);
        }

        return Promise.resolve(map);
    });

    destroy = jest.fn(() => {
        return new Promise<void>((resolve) => {
            this.emit(SyncEvent.Destroyed);
            resolve();
        });
    });
}
