import { interfaces } from "inversify";
import { AccountSidOption, RuntimeDomainOption, PublicConfig } from "~/modules/config";
import { PublicConfigMockOptions, publicConfigMockOptionsRTTI } from "~/modules/config/mock";
import { XOR } from "~/utils/utilityTypes";

export async function getPublicConfigMock(
    container: interfaces.Container,
    
    option: XOR<AccountSidOption, RuntimeDomainOption> 
): Promise<PublicConfig> {
    let mockPublicConfig: PublicConfig = {
        runtimeDomain: "runtimeDomain",
        accountSid: "accountSid",
        publicAttributes: { loginPopup: true }
    };

    const hasMockOptions = container.isBound(publicConfigMockOptionsRTTI);
    if (hasMockOptions) {
        const mockOptions = container.get<PublicConfigMockOptions>(publicConfigMockOptionsRTTI);
        if (mockOptions && mockOptions.publicConfig) {
            mockPublicConfig = mockOptions.publicConfig;
        }
    }

    return Promise.resolve(mockPublicConfig);
}
