import { interfaces } from "inversify";
import { environmentConfigRTTI, EnvironmentConfig } from "~/modules/config";
import { twilioIdpUrlMockOptionsRTTI, TwilioIdpUrlMockOptions } from "~/modules/auth/mock";

export function getTwilioIdpUrlMock(container: interfaces.Container, port?: string): string {
    const envConfig = container.get<EnvironmentConfig>(environmentConfigRTTI);

    let twilioServiceLoginUrl = envConfig.twilioServiceLoginUrl;

    const hasMockOptions = container.isBound(twilioIdpUrlMockOptionsRTTI);
    if (hasMockOptions) {
        const mockOptions = container.get<TwilioIdpUrlMockOptions>(twilioIdpUrlMockOptionsRTTI);
        if (mockOptions && mockOptions.twilioIdpUrl) {
            twilioServiceLoginUrl = mockOptions.twilioIdpUrl;
        }
    }

    return port ? `${twilioServiceLoginUrl}?localPort=${port}` : twilioServiceLoginUrl;
}
