import { interfaces } from "inversify";
import { telemetryClientRTTI } from "~/modules/telemetry";
import { TelemetryClientMock } from "./TelemetryClient.mock";

export function createTelemetryClientMock<U extends object>(
    container: interfaces.Container,
    name: string
): TelemetryClientMock<U> {
    const telemetryClient = container.get<TelemetryClientMock<U>>(telemetryClientRTTI);
    telemetryClient.name = name;
    return telemetryClient;
}
