import "reflect-metadata";

import { Container } from "inversify";
import { getContainer } from "./ioc.config";

import { authModuleContainerMock } from "./modules/auth/auth.ioc.mock";
import { sessionModuleContainerMock } from "./modules/session/session.ioc.mock";
import { clientModuleContainerMock } from "./modules/client/client.ioc.mock";
import { eventsModuleContainerMock } from "./modules/events/events.ioc.mock";
import { configModuleContainerMock } from "./modules/config/config.ioc.mock";
import { websocketModuleContainerMock } from "./modules/websocket/websocket.ioc.mock";
import { loggerModuleContainerMock } from "./modules/logger/logger.ioc.mock";
import { syncModuleContainerMock } from "./modules/sync/sync.ioc.mock";
import { telemetryModuleContainerMock } from "./modules/telemetry/telemetry.ioc.mock";
import { telemetrySdkClientModuleContainerMock } from "./modules/telemetrySdkClient/telemetrySdkClient.ioc.mock";
import { statsModuleContainerMock } from "./modules/stats/stats.ioc.mock";
import { withContainerMock } from "./backend/openApi.container.mock";





export class MockContainer extends Container {
    









    constructor() {
        super();

        
        this.loadModules();

        
        const originalContainer = getContainer();
        this.parent = originalContainer;
    }

    private loadModules() {
        this.load(authModuleContainerMock);
        this.load(configModuleContainerMock);
        this.load(sessionModuleContainerMock);
        this.load(clientModuleContainerMock);
        this.load(eventsModuleContainerMock);
        this.load(websocketModuleContainerMock);
        this.load(loggerModuleContainerMock);
        this.load(syncModuleContainerMock);
        this.load(telemetryModuleContainerMock);
        this.load(telemetrySdkClientModuleContainerMock);
        this.load(statsModuleContainerMock);
        withContainerMock(this);
    }

    







    bindOriginal(symbol: symbol) {
        this.unbind(symbol);
    }
}
