import { EventEmitter } from "events";

export const { TwilsockClient: TwilsockClientMock } = jest.genMockFromModule("twilsock");

TwilsockClientMock.prototype.connect.mockImplementation();
TwilsockClientMock.prototype.on.mockImplementation(EventEmitter.prototype.on);
TwilsockClientMock.prototype.emit.mockImplementation(EventEmitter.prototype.emit);
Object.defineProperty(TwilsockClientMock.prototype, "isConnected", {
    value: false,
    writable: true
});
