import { ContainerModule, interfaces } from "inversify";
import { telemetryEventGroupFactoryRTTI, telemetryProcessorRTTI } from "./telemetry.rtti";
import { TelemetryClientMock, createTelemetryClientMock, TelemetryEventGroupMock } from "./mock";
import { TelemetryProcessorMock } from "./TelemetryProcessor/TelemetryProcessorMock/TelemetryProcessor.mock";
import { createEventGroupMock } from "./TelemetryEventGroup/TelemetryEventGroupMock/createEventGroup.mock";
import {
    TelemetryClientFactory,
    TelemetryClient,
    TelemetryEventGroup,
    telemetryEventGroupRTTI,
    telemetryClientFactoryRTTI,
    telemetryClientRTTI
} from "~/modules/telemetry";
import { TelemetryProcessor } from "./TelemetryProcessor/TelemetryProcessor";
import { TelemetryEventGroupFactory } from "./TelemetryEventGroupFactory/TelemetryEventGroupFactory";

export const telemetryModuleContainerMock = new ContainerModule(<U extends object>(bind: interfaces.Bind) => {
    bind<TelemetryClientFactory<U>>(telemetryClientFactoryRTTI).toFactory((context: interfaces.Context) => {
        return (name: string) => {
            return createTelemetryClientMock(context.container, name);
        };
    });

    bind<TelemetryClient<U>>(telemetryClientRTTI).to(TelemetryClientMock).inSingletonScope();

    bind<TelemetryEventGroupFactory<U>>(telemetryEventGroupFactoryRTTI).toFactory((context: interfaces.Context) => {
        return (telemetryClientAttributes: TelemetryClient<object>, name?: string) => {
            return createEventGroupMock(context.container, name);
        };
    });

    bind<TelemetryEventGroup<U>>(telemetryEventGroupRTTI).to(TelemetryEventGroupMock);

    bind<TelemetryProcessor>(telemetryProcessorRTTI).to(TelemetryProcessorMock).inSingletonScope();
});
