import { SyncMapItem, Subscribable } from "~/modules/sync";
import { InternalError } from "~/errors";
import { createSubscribable } from "~/modules/sync/Subscribable/createSubscribable";

export class SyncMapItemMock<T extends object = object> implements SyncMapItem<T> {
    
    constructor(key: string, value?: T) {
        this.key = key;
        this.value = value || ({} as T);
    }

    private updateHandler?: Function;

    readonly key: string;

    value: object;

    private readonly subscribableOptions = {
        mapKeysToCamelCase: true,
        dateFields: ["timestampUpdated"]
    };

    getSubscribable = jest.fn(() => {
        return createSubscribable(this.value, this.subscribableOptions, this.subscribe, this.unsubscribe);
    });

    getSubscribableForSubKey = jest.fn(
        <K extends keyof T>(subKey: K): Subscribable<T[K]> => {
            
            
            const subValue = this.value[subKey];
            if (typeof subValue === "undefined") {
                throw new InternalError(`Key ${subKey} does not exist in item ${this.key}`);
            }
            
            const mappedValue = createSubscribable<T[K]>(
                subValue,
                this.subscribableOptions,
                this.subscribe,
                this.unsubscribe
            );
            return mappedValue;
        }
    );

    subscribe = jest.fn((cb) => {
        this.updateHandler = cb;
    });

    unsubscribe = jest.fn(() => {
        this.updateHandler = undefined;
    });

    $update = jest.fn((newValue) => {
        this.value = newValue;
        if (this.updateHandler) {
            this.updateHandler();
        }
    });
}
