import { EventEmitter } from "events";
import { SyncClientState, SyncClient, SyncClientEvent } from "./SyncClient";

const { SyncClient: SyncClientMock } = jest.genMockFromModule("twilio-sync");

SyncClientMock.prototype.on.mockImplementation(EventEmitter.prototype.on);
SyncClientMock.prototype.emit.mockImplementation(EventEmitter.prototype.emit);
SyncClientMock.prototype.shutdown.mockImplementation();
Object.defineProperty(SyncClientMock.prototype, "connectionState", {
    value: SyncClientState.Disconnected,
    writable: true
});

const $setConnectionState = (state: SyncClientState) => {
    SyncClientMock.prototype.connectionState = state;
};

const $getEventEmitHandler = (newState: SyncClientState) => {
    const mockedHandler = {
        apply: (target: Function, thisArg: SyncClient, argumentsList: [string]) => {
            target.call(thisArg, ...argumentsList);
            if (argumentsList[0] === SyncClientEvent.ConnectionStateChanged) {
                thisArg.emit(argumentsList[0], newState);
            }
        }
    };
    return mockedHandler;
};

export { $setConnectionState, $getEventEmitHandler, SyncClientMock };
