import { publicConfigProviderRTTI, PublicConfigProvider } from "./modules/config";
import { PublicConfigMockOptions, publicConfigMockOptionsRTTI } from "./modules/config/mock";
import { clientProviderRTTI, ClientProvider } from "./modules/client";
import { clientMockOptionsRTTI, ClientMockOptions } from "./modules/client/mock";
import { authenticatorFactoryRTTI, twilioIdpUrlRTTI, TwilioIdpUrlFunction } from "./modules/auth";
import {
    authenticatorMockOptionsRTTI,
    AuthenticatorMockOptions,
    AuthenticatorMockFactory,
    TwilioIdpUrlMockOptions,
    twilioIdpUrlMockOptionsRTTI
} from "./modules/auth/mock";
import { LoggerFactory, loggerFactoryRTTI } from "./modules/logger";
import { MockContainer } from "./ioc.config.mock";




export type MockFactory<M, O = undefined> = (options?: O) => M;

const makeMockFactory = <M, O>(mockRTTI: symbol, optionsRTTI?: symbol): MockFactory<M, O> => (options): M => {
    const container = new MockContainer();
    if (optionsRTTI && options) {
        container.bind(optionsRTTI).toConstantValue(options);
    }
    return container.get(mockRTTI);
};




export type Mocks = {
    auth: MockFactory<AuthenticatorMockFactory, AuthenticatorMockOptions>;
    getLogger: MockFactory<LoggerFactory<string>>;
    getTwilioIdpUrl: MockFactory<TwilioIdpUrlFunction, TwilioIdpUrlMockOptions>;
    getPublicConfig: MockFactory<PublicConfigProvider, PublicConfigMockOptions>;
    makeCreateClient: MockFactory<ClientProvider, ClientMockOptions>;
};

const mocks: Mocks = {
    auth: makeMockFactory(authenticatorFactoryRTTI, authenticatorMockOptionsRTTI),
    getLogger: makeMockFactory(loggerFactoryRTTI),
    getTwilioIdpUrl: makeMockFactory(twilioIdpUrlRTTI, twilioIdpUrlMockOptionsRTTI),
    getPublicConfig: makeMockFactory(publicConfigProviderRTTI, publicConfigMockOptionsRTTI),
    makeCreateClient: makeMockFactory(clientProviderRTTI, clientMockOptionsRTTI)
};




export const getMock = <T extends keyof Mocks>(name: T, config?: Parameters<Mocks[T]>[0]): ReturnType<Mocks[T]> => {
    


    
    return (<Function>mocks[name])(config);
};

export { ClientMockOptions } from "./modules/client/mock";
export { PublicConfigMockOptions } from "./modules/config/mock";
export {
    TwilioIdpUrlMockOptions,
    AuthenticatorMockFactory,
    AuthenticatorMock,
    AuthenticatorMockOptions
} from "./modules/auth/mock";
