import { inject } from "inversify";
import { TwilsockClient } from "twilsock";
import { EventPublisher } from "~/modules/events";
import { Twilsock } from "./Twilsock";
import { TwilsockEvent } from "./TwilsockEvent";
import { newableTwilsockClientRTTI } from "~/modules/websocket/websocket.rtti";
import { NewableTwilsockClient } from "~/modules/websocket/TwilsockClient/TwilsockClient";

export class TwilsockMock extends EventPublisher<TwilsockEvent> implements Twilsock {
    private readonly NewableTwilsockClient: NewableTwilsockClient;

    private twilsockClient: TwilsockClient;

    constructor(@inject(newableTwilsockClientRTTI) newableTwilsockClient: NewableTwilsockClient) {
        super();
        this.NewableTwilsockClient = newableTwilsockClient;
    }

    connect = jest.fn(() => {
        return Promise.resolve();
    });

    destroy = jest.fn(() => {
        this.$emit(TwilsockEvent.Disconnected);
        this.removeAllListeners();
        return Promise.resolve();
    });

    updateToken = jest.fn(() => {
        return Promise.resolve();
    });

    getRawTwilsockClient = jest.fn(() => {
        if (!this.twilsockClient) {
            this.twilsockClient = new this.NewableTwilsockClient("token", "productID");
        }
        return this.twilsockClient;
    });

    $emit(event: TwilsockEvent, ...args: unknown[]): boolean {
        return super.emit(event, ...args);
    }

    post = jest.fn(() => {
        return Promise.resolve({
            status: {
                code: 204,
                status: "No content"
            },
            headers: {}
        });
    });
}
