import { ContainerModule, interfaces } from "inversify";
import { newableSyncClientRTTI, syncRTTI } from "./sync.rtti";
import { NewableSyncClient } from "./SyncClient/SyncClient";
import { Sync } from "./Sync/Sync";
import { SyncClientMock } from "./SyncClient/SyncClient.mock";
import { SyncMock } from "./Sync/Sync.mock";

export const syncModuleContainerMock = new ContainerModule((bind: interfaces.Bind) => {
    bind<NewableSyncClient>(newableSyncClientRTTI).toConstructor(SyncClientMock);
    bind<Sync>(syncRTTI).to(SyncMock);
});
