import { ContainerModule, interfaces } from "inversify";
import { telemetrySdkClientRTTI } from "./telemetrySdkClient.rtti";
import { TelemetrySdkClient } from "./TelemetrySdkClient/TelemetrySdkClient";
import { TelemetrySDKSessionData } from "~/modules/telemetrySdkClient/TelemetrySdkClient/TelemetrySDKSessionData";
import { createTelemetryClientMock } from "~/modules/telemetry/mock";

export const telemetrySdkClientModuleContainerMock = new ContainerModule((bind: interfaces.Bind) => {
    bind<TelemetrySdkClient>(telemetrySdkClientRTTI)
        .toDynamicValue((context: interfaces.Context) => {
            return createTelemetryClientMock<TelemetrySDKSessionData>(context.container, "sdk-v1");
        })
        .inSingletonScope();
});
