import { ContainerModule, interfaces } from "inversify";
import { realtimeStatsRTTI, newableQueueStatsRTTI, newableTasksByTimeRTTI } from "./stats.rtti";
import { RealtimeStats } from "./RealtimeStats/RealtimeStats";
import { RealtimeStatsMock } from "./RealtimeStats/RealtimeStats.mock";
import { NewableQueueStats } from "./RealtimeStats/QueueStats/NewableQueueStats";
import { QueueStatsMock } from "./RealtimeStats/QueueStats/QueueStatsMock/QueueStats.mock";
import { NewableTasksByTime } from "./RealtimeStats/QueueStats/TasksByTime/NewableTasksByTime";
import { TasksByTimeMock } from "./RealtimeStats/QueueStats/TasksByTime/TasksByTime.mock";

export const statsModuleContainerMock = new ContainerModule((bind: interfaces.Bind) => {
    bind<RealtimeStats>(realtimeStatsRTTI).to(RealtimeStatsMock).inSingletonScope();
    bind<NewableQueueStats>(newableQueueStatsRTTI).toConstructor(QueueStatsMock);
    bind<NewableTasksByTime>(newableTasksByTimeRTTI).toConstructor(TasksByTimeMock);
});
