import { injectable, inject, postConstruct, optional } from "inversify";
import { Authenticator, IdpConfig, TokenData } from "~/modules/auth";

import { authenticatorMockOptionsRTTI } from "~/modules/auth/auth.rtti";
import { AuthenticatorMockOptions } from "./AuthenticatorMockOptions";




@injectable()
export class AuthenticatorMock implements Authenticator {
    private readonly mockOptions: AuthenticatorMockOptions;

    private $idpUrl: string;

    constructor(@inject(authenticatorMockOptionsRTTI) @optional() mockOptions: AuthenticatorMockOptions) {
        this.mockOptions = mockOptions;
    }

    @postConstruct()
    private onAuthenticatorConstructed() {
        const onConstructed = this.mockOptions?.authenticatorConstructed;
        if (onConstructed) {
            onConstructed(this);
        }
    }

    validateToken = jest.fn(
        (): Promise<TokenData> => {
            return Promise.resolve({
                roles: ["agent"],
                valid: true
            });
        }
    );

    refreshToken = jest.fn((token: string) => {
        const mockToken = this.mockOptions?.refreshedToken || `token ${token}`;
        return Promise.resolve(mockToken);
    });

    getIdpUrl = jest.fn((config: IdpConfig) => {
        const url = this.$idpUrl || `idp ${config.redirectUrl}`;
        return Promise.resolve(url);
    });

    setAccountSid = jest.fn();

    $setIdpUrl(url: string) {
        this.$idpUrl = url;
    }
}
