import { interfaces } from "inversify";
import { telemetryEventGroupRTTI } from "~/modules/telemetry";
import { TelemetryEventGroupMock } from "~/modules/telemetry/mock";

export function createEventGroupMock<T>(container: interfaces.Container, name?: string): TelemetryEventGroupMock<T> {
    const eventGroup = container.get<TelemetryEventGroupMock<T>>(telemetryEventGroupRTTI);
    eventGroup.name = name;
    return eventGroup;
}
